import React, { useState, useEffect } from "react";

import Header from "../../components/Header";
import Container from "../../components/Container";
import Hello from "../../components/Hello";
import Stack from "../../components/Stack";
import Projects from "../../components/Projects";
import Certificates from "../../components/Certificates";
import Contacts from "../../components/Contacts";
import Footer from "../../components/Footer";

import "./index.scss";

const Main = () => {
    const [display, setDisplay] = useState(false);
    const [open, setOpen] = useState(false);

    useEffect(() => {
        setTimeout(() => setDisplay(true), 2.5 * 1000);
    }, []);

    return (
        <React.Fragment>
            <Header display={display} open={open} setOpen={setOpen} />
            <main className={open ? "active_menu" : null}>
                <Container>
                    <Hello display={true} />
                    <Stack display={display} />
                    <Certificates display={display} />
                    <Projects display={display} />
                    <Contacts display={display} />
                    <Footer display={display} />
                </Container>
            </main>
        </React.Fragment>
    );
};

export default Main;
