import React, { useEffect, useState } from "react";

import "./index.scss";

const shuffle = a => {
    const b = [...a];
    for (let i = b.length - 1; i > 0; i--) {
        const j = Math.floor(Math.random() * (i + 1));
        [b[i], b[j]] = [b[j], b[i]];
    }
    return b;
};

const StackCloud = ({ stack }) => {
    const [words, setWords] = useState(shuffle(stack.map(item => ({ display: false, value: item }))));
    useEffect(() => {
        (async () => {
            await new Promise(res => setTimeout(res, 300));
            const word = shuffle(words).find(item => !item.display);

            if (word) {
                const new_state = [...words];
                const index = new_state.findIndex(item => item.value === word.value);
                new_state.splice(index, 1, { display: true, value: word.value });
                setWords(new_state);
            }
        })();
    }, [words]);
    return (
        <article className="stack-cloud">
            {words.map(({ display, value }) => {
                return (
                    <div
                        key={value}
                        className={
                            display ? "stack-cloud__element stack-cloud__element--active" : "stack-cloud__element"
                        }
                    >
                        {value}
                    </div>
                );
            })}
        </article>
    );
};

export default StackCloud;
